<script>

import CoreService from '@services/core'
import { toPrice } from '@plugins/money'

export default {

  filters: {
    toMoneyFormat: function (value) {
      if (!value) return 0.00

      return toPrice(value).toFormat('0,0.00')
    },
  },
  props: {
    invoice: {
      type: Object,
      required: true,
    },
  },

  data () {
    return {
      form: new window.Form({
        ledger_id: this.invoice.id,
        item: null,
        // quantity: 1,
        unit_price: 0,
        // date: formatDate(new Date(), 'yyyy-MM-dd'),
        // comments: '',
        items: [],
      }),

      discountItems: [],

      discountCalculations: [
        { name: 'Fixed Amount', id: 0 },
        { name: 'Percentage of Bill', id: 1 },
      ],
      selectedCalculation: 0,
      selectedPercentage: 0,

    }
  },

  computed: {
    billsTable () {
      return this.$refs.billsTable
    },

    netBalance () {
      if (this.selectedCalculation === 0) {
        return (Number.parseInt(this.invoice.balance * 0.01)) - Number.parseInt(this.form.unit_price)
      }

      const percentage = this.selectedPercentage * 0.01

      const discountGiven = (this.invoice.balance * 0.01) * percentage

      this.form.unit_price = Math.round(discountGiven)

      return (this.invoice.balance * 0.01) - Math.round(discountGiven)
      // const discount = toPrice(this.totalBill).multiply(percentage)

      // return toPrice(this.totalBill).subtract(discount).toFormat('0,0.00')
    },
  },

  methods: {
    applyDiscount () {
      if (!this.form.item) {
        this.$toast.warning('Please select the discount type')
        return
      }

      if (this.selectedCalculation === 0) {
        // fixed amount
        if (this.form.unit_price > this.totalBill || this.form.unit_price === 0) {
          this.$toast.error('Discount cannot be zero or greater than total bill')
          return
        }

        // this.saveDiscount()
      }

      if (this.selectedCalculation === 1) {
        // percentage based
        if (this.selectedPercentage > 100 || this.selectedPercentage <= 0) {
          this.$toast.warning('The discount rate exceeds the allowed range')
          return
        }

        this.saveDiscount()
      }
    },

    saveDiscount () {
      this.form.items.push({
        item_id: this.form.item.id,
        description: this.form.item.name,
        unit_price: this.form.unit_price,
        quantity: 1,
      })

      this.form.post('/patient-bills')
        .then(() => {
          this.$toast.success('Discount Applied')

          this.$emit('bill:added')

          this.$bvModal.hide('discount-modal')
        })
        .catch()
    },

    setup () {
      this.findAllDiscountProducts()
    },

    cleanup () {
      this.form.reset()
      this.selectedCalculation = 0
      this.selectedPercentage = 0
      this.totalBill = 0
    },

    findAllDiscountProducts () {
      CoreService.findAllItems({
        params: {
          filter: {
            discount: true,
          },
        },
      }).then(({ data }) => {
        this.discountItems = data.data
      }).catch(error => {
        console.error(error)
      })
    },
  },
}
</script>

<template>
  <b-modal
    id="discount-modal"
    title-class="font-18"
    no-close-on-backdrop
    footer-class="d-flex justify-content-between bg-muted-lt border-top"
    content-class="bg-white rounded-3"
    body-class="px-4"
    modal-class="modal-blur"
    title="Discount Allowed"
    hide-header-close
    no-close-on-esc
    no-enforce-focus
    @shown="setup"
    @hidden="cleanup"
  >
    <div class="form-group mb-4">
      <label for="" class="form-label">Discount type</label>

      <v-select
        id="account_id"
        v-model="form.item"
        :options="discountItems"
        label="name"
        :reduce="option => option"
        :clearable="false"
      />

      <span
        v-if="form.hasError('item_id')"
        class="text-danger"
        v-text="form.getError('item_id')"
      />
    </div>

    <div class="form-group mb-4">
      <label for="" class="form-label">Discount Method</label>

      <div>
        <label class="form-check form-check">
          <input
            v-model="selectedCalculation" class="form-check-input" type="radio"
            checked="" :value="0"
          >
          <span class="form-check-label">Fixed Amount </span>
        </label>
        <label class="form-check form-check">
          <input
            v-model="selectedCalculation" class="form-check-input" type="radio"
            :value="1"
          >
          <span class="form-check-label">Percentage</span>
        </label>
      </div>
    </div>

    <div class="row">
      <div class="col-xl-4">
        <div class="form-group mb-4">
          <label for="" class="form-label">Bill</label>
          <medic-money
            :disabled="true"
            :value="invoice.balance * 0.01"
          />
        </div>
      </div>
      <div v-if="selectedCalculation === 0" class="col-xl-4">
        <div class="form-group mb-4">
          <label for="" class="form-label">Amount</label>
          <medic-money
            @input="function(payload) {
              form.unit_price = payload
            }"
          />
        </div>
      </div>
      <div v-if="selectedCalculation === 1" class="col-xl-4">
        <div class="form-group mb-4">
          <label for="disc_rate" class="form-label">Rate (%)</label>
          <input
            id="disc_rate"
            v-model="selectedPercentage"
            type="number" class="form-control" min="1"
            max="100"
          >
          <span class="text-indigo small">Kes {{ form.unit_price | toMoneyFormat }}</span>
        </div>
      </div>
      <div class="col-xl-4">
        <div class="form-group mb-4">
          <label for="" class="form-label">Net Amount</label>

          <medic-money
            :value="netBalance"
            disabled
          />
        </div>
      </div>
    </div>

    <template #modal-footer="{cancel}">
      <button
        :disabled="form.processing"
        class="btn btn-light"
        @click.prevent="cancel"
      >
        Close
      </button>

      <button
        :disabled="form.processing"
        type="submit"
        class="btn btn-primary"
        @click.prevent="applyDiscount"
      >
        Give Discount
      </button>
    </template>
  </b-modal>
</template>
